.app {
      
    &__form {
        flex: none;
    }

    &__overlay {
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,0.5);
        z-index: 2;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__title {
        font-size: 3rem;
        padding: 15px ;
        margin-bottom: 15px;
        text-align: center;
    }
}