/* sriracha-regular - latin */
@font-face {
    font-family: 'Sriracha';
    font-style: normal;
    font-weight: 400;
    src: url('./assets/fonts/sriracha-v4-latin-regular.eot'); /* IE9 Compat Modes */
    src: local('Sriracha'), local('Sriracha-Regular'),
         url('./assets/fonts/sriracha-v4-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('./assets/fonts/sriracha-v4-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
         url('./assets/fonts/sriracha-v4-latin-regular.woff') format('woff'), /* Modern Browsers */
         url('./assets/fonts/sriracha-v4-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
         url('./assets/fonts/sriracha-v4-latin-regular.svg#Sriracha') format('svg'); /* Legacy iOS */
  }